.section-video {

}

.video-title {
  margin: 0 0 40px;
  color: #464f56;
  font-size: 16px;
  text-align: center;
}

.video {
  position: relative;
  max-width: 650px;
  height: 315px;
  margin: auto;
  border-radius: 6px;
  background-color: rgb(37, 37, 142);
  background-position: center;
  box-shadow: 0px 0px 42px 0px rgba(0, 0, 0, 0.28);
}

.video_play {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: url('../img/video-play.png') no-repeat;
  margin: auto;
  height: 123px;
  width: 123px;
  cursor: pointer;
}

.video iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 6px;
}
